* {
  box-sizing: border-box;
}

body {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  padding-top: 180px;
  overflow-x: hidden;
}

#app {
  min-width: 0;
  max-width: 100%;
  box-shadow: none;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

.header {
  width: 100%;
  max-width: 100%;
  min-height: auto;
  margin: 0;
  padding: 16px 16px 12px;
}

.main-layout {
  flex-direction: column;
  gap: 20px;
  padding: 0 16px;
}

.main-content-left, .main-content-center, .main-content-right {
  width: 100%;
  position: static;
}

.main-content-center {
  order: -1;
}

.date-time-section {
  z-index: 100;
  width: calc(100vw - 20px);
  max-width: 200px;
  position: fixed;
  bottom: 10px;
  left: 10px;
}

.quote-section {
  z-index: 100;
  width: calc(100vw - 20px);
  max-width: 200px;
  position: fixed;
  bottom: 10px;
  right: 10px;
}

.header-top {
  flex-direction: column;
  gap: 16px;
  margin-bottom: 16px;
  padding: 0 8px;
}

.header-left {
  justify-content: center;
}

.app-title {
  text-align: center;
  font-size: 1.8rem;
}

.header-right {
  justify-content: center;
}

.language-btn-header {
  gap: 6px;
  padding: 10px 14px;
  font-size: 13px;
}

.language-dropdown-header {
  min-width: 180px;
  left: 50%;
  right: auto;
  transform: translateX(-50%)translateY(-10px);
}

.language-dropdown-header.show {
  transform: translateX(-50%)translateY(0);
}

.header-title {
  text-align: center;
  margin-bottom: 18px;
  font-size: 1.6rem;
  font-weight: 800;
  line-height: 1.2;
}

.header-searchbar {
  border-radius: 16px;
  width: 100%;
  max-width: 90vw;
  margin: 12px auto;
  padding: 8px 12px;
}

.search-input-wrapper {
  border-radius: 16px;
  width: 100%;
  max-width: 100%;
  min-height: 44px;
}

.search-input-rounded {
  border-radius: 16px;
  width: 100%;
  min-height: 44px;
  padding: 12px 16px 12px 44px;
  font-size: .95rem;
}

.searchbar-icon-inside {
  font-size: 1.2rem;
  left: 16px;
}

.search-btn-inside {
  border-radius: 16px;
  width: 100%;
  min-width: 100%;
  height: 44px;
  min-height: 44px;
  margin-top: 8px;
  position: static;
  right: 10px;
}

.search-results-container {
  max-height: 250px;
  margin-top: 4px;
}

.search-result-item {
  padding: 10px 12px;
}

.search-result-text {
  font-size: 13px;
}

.search-result-add {
  width: 20px;
  height: 20px;
  font-size: 14px;
}

.pagination-controls {
  padding: 8px 12px;
}

.pagination-info {
  font-size: 11px;
}

.pagination-arrow {
  width: 28px;
  height: 28px;
  font-size: 16px;
}

.header-cities-chips {
  flex-wrap: wrap;
  justify-content: center;
  gap: 8px;
  margin: 12px 8px 0;
}

.header-city-chip {
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  min-height: 36px;
  padding: 8px 16px;
  font-size: .9rem;
  display: flex;
}

.scroll-arrow {
  width: 28px;
  height: 28px;
  font-size: 16px;
}

.scroll-left {
  left: -20px;
}

.scroll-right {
  right: -20px;
}

.country-button {
  display: none !important;
}

#nav-buttons {
  flex-wrap: wrap;
  justify-content: center;
  gap: 8px;
  margin: 16px auto;
  padding: 0 12px;
}

.nav-btn {
  border-radius: 20px;
  flex: 1;
  min-width: 44px;
  max-width: 120px;
  min-height: 44px;
  margin: 4px;
  padding: 12px 20px;
  font-size: .9rem;
}

#main-content {
  padding: 16px 12px 24px;
}

.today-weather-card {
  width: 100%;
  max-width: 95%;
  margin: 16px auto;
  position: static;
  transform: none;
}

.weather-card-overlay {
  border-radius: 24px;
  min-width: auto;
  max-width: 100%;
  padding: 24px 20px 20px;
}

.weather-card-header {
  gap: 12px;
  margin-bottom: 16px;
}

.weather-city {
  font-size: 1.2rem;
}

.weather-icon {
  width: 48px;
  height: 48px;
}

.weather-temp-main {
  margin: 8px 0 4px;
  font-size: 2.4rem;
}

.weather-minmax {
  margin-bottom: 8px;
  font-size: 1rem;
}

.weather-desc {
  margin-top: 8px;
  font-size: 1rem;
}

.five-days-forecast {
  border-radius: 28px;
  width: calc(100% - 40px);
  max-width: calc(100% - 40px);
  margin: 20px 20px 0;
  padding: 32px 24px 28px;
}

.forecast-location-header {
  text-align: center;
  margin-bottom: 24px;
  padding: 0 16px;
}

.forecast-city-name {
  color: #fff;
  letter-spacing: .5px;
  margin-bottom: 4px;
  font-size: 1.4rem;
  font-weight: 700;
}

.forecast-country-name {
  color: #ffd54f;
  opacity: .9;
  letter-spacing: .3px;
  font-size: 1rem;
  font-weight: 500;
}

.forecast-cards {
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch;
  flex-wrap: nowrap;
  gap: 12px;
  margin: 0 -8px;
  padding-bottom: 8px;
  display: flex;
  overflow-x: auto;
}

.forecast-card {
  scroll-snap-align: start;
  touch-action: pan-x;
  border-radius: 20px;
  flex-shrink: 0;
  min-width: 140px;
  max-width: 160px;
  margin-right: 0;
  padding: 16px 12px 12px;
}

.forecast-day {
  margin-bottom: 4px;
  font-size: .7rem;
}

.forecast-date {
  margin-bottom: 8px;
  font-size: .9rem;
}

.forecast-card img {
  width: 36px;
  height: 36px;
  margin-bottom: 8px;
}

.forecast-minmax {
  margin-bottom: 6px;
  font-size: .6rem;
}

.forecast-minmax span {
  font-size: .6rem;
}

.minmax-divider {
  height: 16px;
  margin: 0 3px;
}

.forecast-desc {
  margin-top: 6px;
  font-size: .65rem;
}

.more-info-btn {
  border-radius: 12px;
  width: 70%;
  min-width: 60px;
  margin-bottom: 8px;
  padding: 6px 0;
  font-size: .65rem;
}

.more-info-section {
  gap: 6px;
  max-height: 200px;
  margin-top: 8px;
  padding: 8px 4px;
  overflow-y: auto;
}

.more-info-mini-card {
  min-width: 70px;
  max-width: 90px;
  height: 120px;
  margin: 0 4px;
  padding: 6px 2px 4px;
  font-size: .6rem;
}

.more-info-mini-card .mini-time {
  margin-bottom: 2px;
  font-size: .6rem;
}

.more-info-mini-card .mini-temp {
  margin: 2px 0;
  font-size: .65rem;
}

.more-info-mini-card .mini-icon {
  width: 10px;
  height: 10px;
  margin-bottom: 2px;
}

.more-info-mini-card .mini-detail {
  gap: 1px;
  font-size: .55rem;
}

.quote-section {
  border-radius: 20px;
  max-width: 95%;
  margin: 24px auto;
  padding: 20px 16px 16px;
  font-size: 1rem;
  position: static;
}

.quote-author {
  margin-top: 8px;
  font-size: .9rem;
}

.date-time-section {
  border-radius: 24px;
  min-width: auto;
  max-width: 95%;
  margin: 24px auto;
  padding: 24px 20px 20px;
  position: static;
}

.date-big, .date-num {
  font-size: 2.4rem;
}

.date-day {
  font-size: 1.6rem;
}

.date-month {
  margin-bottom: 16px;
  font-size: 1rem;
}

.date-time-row {
  gap: 12px;
  margin-top: 8px;
  font-size: 1rem;
}

.date-time-clock {
  font-size: 1rem;
}

.sun-icon, .sunset-icon {
  font-size: 1.2rem;
}

.date-sunrise, .date-sunset {
  font-size: .9rem;
}

.forecast-chart-section {
  border-radius: 20px;
  max-width: 95%;
  margin: 20px auto 0;
  padding: 20px 16px 16px;
}

.forecast-chart {
  border-radius: 12px;
  width: 100%;
  min-width: auto;
  max-width: 100%;
  height: 200px;
}

.show-chart-btn-container {
  margin-top: 20px;
  margin-bottom: 0;
}

#show-chart-btn.nav-btn {
  border-radius: 20px;
  gap: 6px;
  min-height: 48px;
  padding: 14px 32px;
  font-size: 1rem;
}

.chart-btn-text {
  font-size: .95rem;
}

.chart-btn-icon {
  font-size: 1rem;
}

.error-message-overlay {
  min-width: 280px;
  max-width: 90%;
  margin: 16px auto;
  left: 50%;
  transform: translateX(-50%);
}

.loading-overlay {
  padding: 20px;
}

.loading-content {
  border-radius: 16px;
  padding: 24px 20px;
}

.welcome-centered {
  min-height: 50vh;
  padding: 20px;
}

.welcome-message {
  padding: 20px 0;
  font-size: 1.6rem;
}

.weather-right-group {
  z-index: auto;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  width: 100%;
  max-width: 100%;
  margin: 24px auto;
  padding: 0 20px;
  display: flex;
  position: static;
  top: auto;
  right: auto;
}

.main-content-flex {
  flex-direction: column;
  gap: 16px;
  min-height: auto;
}

.main-content-left, .main-content-center, .main-content-right {
  align-items: center;
  width: 100%;
  max-width: 100%;
}

.country-align-row {
  margin-top: 12px;
  margin-bottom: 16px;
}

.country-display {
  border-radius: 20px;
  padding: 6px 16px;
  font-size: .9rem;
}

@media (width <= 599px) {
  #app {
    width: 100%;
    min-height: 100vh;
    margin: 0;
    padding: 0 0 350px;
  }

  .header {
    padding: 16px 16px 12px;
  }

  .weather-right-group {
    gap: 24px;
    margin: 24px auto;
    padding: 0 20px;
