    margin: 16px auto;
    padding: 8px 12px;
  }

  .search-input-wrapper {
    flex-direction: column;
    align-items: stretch;
    gap: 8px;
    width: 100%;
    display: flex;
  }

  .search-input-rounded {
    text-align: left;
    border-radius: 20px;
    width: 100%;
    min-height: 44px;
    padding: 12px 16px 12px 44px;
    font-size: 1rem;
  }

  .search-btn-inside {
    border-radius: 20px;
    width: 100%;
    min-width: 100%;
    height: 44px;
    min-height: 44px;
    margin: 0;
    font-size: .9rem;
    position: static;
    right: auto;
  }

  .today-weather-card {
    margin: 20px auto;
  }

  .forecast-location-header {
    text-align: center;
    margin-bottom: 20px;
    padding: 0 12px;
  }

  .forecast-city-name {
    margin-bottom: 2px;
    font-size: 1.2rem;
  }

  .forecast-country-name {
    font-size: .9rem;
  }

  .weather-card-overlay {
    padding: 28px 24px 24px;
  }

  .weather-temp-main {
    font-size: 2.8rem;
  }

  .forecast-card {
    min-width: 150px;
    max-width: 170px;
    padding: 20px 12px 16px;
  }

  .forecast-day {
    font-size: .75rem;
  }

  .forecast-date {
    font-size: 1rem;
  }

  .forecast-card img {
    width: 40px;
    height: 40px;
  }

  .forecast-minmax, .forecast-minmax span {
    font-size: .65rem;
  }

  .forecast-desc {
    font-size: .7rem;
  }

  .quote-section, .date-time-section {
    margin: 20px auto;
    padding: 24px 20px 20px;
  }

  .date-big, .date-num {
    font-size: 2.8rem;
  }

  .date-day {
    font-size: 1.8rem;
  }

  .forecast-chart {
    height: 180px;
  }

  .error-message-overlay {
    max-width: 95%;
    margin: 12px auto;
    padding: 12px 16px;
  }

  .error-text {
    font-size: .9rem;
  }
}

@media (width >= 600px) and (width <= 899px) {
