    flex-direction: column;
    gap: 10px;
    display: flex;
  }

  .search-input-rounded {
    border-radius: 18px;
    width: 100%;
    min-height: 48px;
    padding: 14px 16px 14px 44px;
  }

  .search-btn-inside {
    border-radius: 20px;
    width: 100%;
    height: 48px;
    margin: 0;
    position: static;
  }

  .forecast-cards {
    overflow-x: unset;
    scroll-snap-type: unset;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px;
    display: grid;
  }

  .forecast-card {
    scroll-snap-align: unset;
    min-width: auto;
    max-width: 100%;
