    gap: 24px;
    padding: 0 20px;
  }

  .main-content-left, .main-content-center, .main-content-right {
    width: 100%;
    position: static;
  }

  .main-content-center {
    order: -1;
  }

  .date-time-section {
    width: 250px;
    max-width: 250px;
    bottom: 20px;
    left: 20px;
  }

  .quote-section {
    width: 250px;
    max-width: 250px;
    bottom: 20px;
    right: 20px;
  }
}

