    min-width: 120px;
    max-width: 140px;
    padding: 10px 8px 6px;
  }

  .forecast-day {
    font-size: .65rem;
  }

  .forecast-date {
    font-size: .8rem;
  }

  .forecast-card img {
    width: 32px;
    height: 32px;
